* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: Arial, Helvetica, sans-serif;
}

.nav-bar {
    background-color: rgb(236, 235, 235);
    width: 100%;
    height: 5vh;
    top: 0;
    position: fixed;
    box-shadow: 0 0 5px rgba(0,0,0,0.5);
    z-index: 999;
    
    ul{
        display: flex;
        justify-content: space-around;
        align-items: center;
        height: 100%;
        width: 40%;
        margin: 0 auto;

        li{
            list-style: none;

            width: 100%;
            display: flex;
            align-items: center;
            justify-content: center;


            a{
                text-decoration: none;
                text-transform: uppercase;
                text-align: center;
                color: black;
                font-weight: 600;
                letter-spacing: 0.5px;
                width: 100%;
                height: 100%;
            }

            .active{
                color: rgb(121, 0, 0);
            }
            


        }
    }
}

